import React,{Component} from 'react'
import styled from 'styled-components'

export default class BackgroundBox extends Component{
    

    render(){
        return(
            <BgBox
                className='wow fadeInDown'
                data-wow-duration="2s"
            >
                <SmallBox/>
                <BigBox/>
            </BgBox>
        )
    }
}
const BgBox = styled.div`
    position: absolute;
`
const SmallBox = styled.div`
    @keyframes SmalBoxAnimation {
        0% {
            transform: rotateZ(27deg) translate(0, 0);
            }
        50% {
            transform: rotateZ(387deg) translate(30%, -25%);
        }
        100%  {
            transform: rotateZ(27deg) translate(0, 0);
            }
    }
    border: 3px solid #4A450F;
    width: 190px;
    height: 190px;
    position: absolute;
    left: 30vw;
    top: 13vh;
    transform: rotateZ(27deg);
    z-index: 0;
    animation: SmalBoxAnimation  180s infinite linear;
`
const BigBox = styled.div`
    @keyframes BigBoxAnimation {
        0% {
            transform: rotateZ(73deg) translate(0, 0);
            }
        50% {
            transform: rotateZ(-248deg) translate(12%, -5%);
        }
        100%  {
            transform: rotateZ(-433deg) translate(0, 0);
            }
    }
    border: 3px solid #4A450F;
    width: 400px;
    height: 400px;
    position: absolute;
    left: 0vw;
    top: 40vh;
    transform: rotateZ(73deg);
    z-index: 0;
    animation: BigBoxAnimation  300s infinite linear;
`