import React, {Component} from 'react'
import styled from 'styled-components'
import { ReactComponent as SvgRightArrow } from './img/right.svg';
import { ReactComponent as SvgLeftArrow } from './img/left.svg';
import Content from './Content'


export default class Slider extends Component{

    
    render(){

        return(
            <SliderBlock>
                <ButtonLeftArrow
                    className='wow fadeInLeft'
                >
                    <SvgLeftArrow/>
                </ButtonLeftArrow>

                <Content/>

                <ButtonRightArrow
                    className='wow fadeInRight'
                    >
                    <SvgRightArrow/>
                </ButtonRightArrow>
            </SliderBlock>
        )
    }
}
const SliderBlock = styled.div`
    user-select: none;
    width: 1400px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    margin-top: 10vh;
    align-items: center;
`
const ButtonLeftArrow = styled.button`
    border: none;
    margin: 0;
    padding: 0;
    background: none;
    outline: none;
    transition: transform 0.1s ease;
    :hover{
        transform: scale(1.1)
    }
    svg{
    }
`
const ButtonRightArrow = styled.button`
    border: none;
    margin: 0;
    padding: 0;
    background: none;
    outline: none;
    transition: transform 0.1s ease;
    :hover{
        transform: scale(1.1)
    }
    svg{

    }
`
