import React, { Component } from 'react'
import styled from 'styled-components'
import './scroll.css'

export default class Scrool extends Component {
    render() { 
        return ( 
            <Scrol>
                <Line>
                    <Scale>
                        <li
                            className = "big true"
                        ></li>
                        <True/>
                        <li
                            className = "small"
                        ></li>
                        <li
                            className = "big"
                        ></li>
                    </Scale>
                </Line>
            </Scrol>
        );
    }
}
const Scrol = styled.div`
`
const Line = styled.div`
    background-color: rgba(255, 255, 255, 0.25);;
    width: 1px;
    height: 250px;
    display: flex;
    align-items: center;
    position: relative;
`

const Scale = styled.ul`
    margin: 0 ;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
    position: absolute;
    transform: translateX(-50%);
    padding: 0;
    li{
        margin-top: 50px;
        :first-child{
            margin-top: 0;
        }
        :nth-child(2){
            margin-top: 0;
        }
    }
`
const True = styled.li`
    width: 16px;
    height: 16px;
    background-color: rgba(255, 244, 223, 0.5);
    border-radius: 20px;
    left: 50%;
    top: 3%;
    transform: translate(-50%, -50%);
    position: absolute;
    margin-top: 0;
    z-index: 2;
    ::before{
        content: '';
        width: 20px;
        height: 20px;
        border-radius: 30px;
        border: 2px solid rgba(255, 244, 223, 0.5);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
    }
`