import React, {Component} from 'react'
import styled from 'styled-components'
import backImg from './img/bacgroundNext.jpg'
import InfoBlock from './InfoBlock'

export default class Portfolio extends Component{
    render(){
        return(
            <PortfolioBlok
                id="portfolio"
            >
                <Bacground/>
                <Label>PORTFOLIO</Label>
                <InfoBlock/>
            </PortfolioBlok>
        )
    }
}
const PortfolioBlok = styled.div`
    height: 100vh;
    width: 100%;
    position: relative;
    background-color: #050505;
    overflow: hidden;
`
const Bacground = styled.div`
    transform: scale(1.1);
    position: absolute;
    width: 100%;
    height: 100%;
    filter: blur(10px) brightness(60%);
    background: url(${backImg}) no-repeat center;
    background-size: cover;
    box-shadow:
        inset 0 100px 50px 0 #050505,
        inset 0 220px 300px 0px #050505;
    z-index: 0;
`

const Label = styled.h3`
    color: white;
    margin: 0;
    padding: 0;
    font-family: sfSemibold;
    font-size: 24px;
    width: max-content;
    position:relative;
    margin: 0 auto;
    top: 5vh;
    user-select: none;
    z-index: 2;
    ::before{
        content:'';
        background-color: white;
        width: 163px;
        height: 2px;
        position:absolute;
        left: -13px;
        bottom: -5px;
    }
`