import React, {Component} from 'react'
import styled from 'styled-components'
import back from './img/backLion.jpg'
import MenuPlusScrool from './Components/MenuPlusScrool'
import Launge from './Components/Launge'
import Content from './Components/Content'
import BackgroundBox from './Components/BackgroundBox'
import MouseScrollDownImg from './img/mouseScrollDown.png'


export default class Header extends Component{

    render(){
        const {Language,ActiveLanguage, newLaunge} = this.props
        
        return(
            <Head
                id='header'
            >
                <BackGrounImg
                    className="wow fadeInRight"
                    data-wow-delay="0.3s"
                />
                <Launge
                    ActiveLanguage = {ActiveLanguage}
                    newLaunge={(newLaungeName)=>{
                        newLaunge(newLaungeName)
                    }}
                />
                <BackgroundBox/>
                <Content
                    Language = {Language['Content']}
                />
                <MouseScrollDown
                    className='wow fadeIn'
                    src={MouseScrollDownImg}
                />
                <MenuPlusScrool/>
            </Head>
        )
    }
}

const Head = styled.div`
    background-color: #050505;
    background-size: cover;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
`
const BackGrounImg = styled.div`
    background: url(${back}) no-repeat center;
    background-size: cover;
    position: absolute;
    height: 100vh;
    width: 100%;
    right: -150px;
    z-index:0;
`
const MouseScrollDown = styled.img`
    position: absolute;
    left: 50%;
    bottom: 3vh;
    transform: translate(-50%, 0);
    width: 20px;
`