import React, {Component} from 'react'
import styled from 'styled-components'
import WeAre from './WeAre'
import Slider from './Slider' 

export default class AboutUs extends Component{
    render(){
        return(
            <About
                id="aboutUs"
            >
                <Label
                    className="wow fadeInUp"
                >ABOUT US</Label>
                <WeAre/>
                <Slider/>
            </About>
        )
    }
}
let About = styled.div`
    background-color: #050505;
    height: 800px;
`
const Label = styled.h3`
    color: white;
    margin: 0;
    padding: 0;
    font-family: sfSemibold;
    font-size: 24px;
    width: max-content;
    position:relative;
    margin: 0 auto;
    /* top: 8vh; */
    user-select: none;
    ::before{
        content:'';
        background-color: white;
        width: 155px;
        height: 2px;
        position:absolute;
        left: -13px;
        bottom: -5px;
    }
`