import React, {Component} from 'react'
import styled from 'styled-components'
import Active from './img/Active.svg';
import DontActive  from './img/dontActive.svg';

export default class InfoBlock extends Component{
    render(){
        return(
            <Info>
                <Description>
                    <DescriptionTitle>NEXT</DescriptionTitle>
                    <Text>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Sem fringilla ut morbi tincidunt augue interdum velit. Mauris pellentesque pulvinar pellentesque habitant. Nisl vel pretium lectus quam id leo in. Platea dictumst vestibulum rhoncus est pellentesque elit ullamcorper dignissim. Habitant morbi tristique senectus et netus et. Phasellus egestas tellus rutrum tellus. Vitae et leo duis ut diam quam nulla. Egestas erat imperdiet sed euismod nisi porta lorem mollis aliquam. Eget magna fermentum iaculis eu non diam. Tellus molestie nunc non 
                    </Text>
                </Description>
                <div>
                    <TechnologiesTitle>TECHNOLOGY</TechnologiesTitle>
                    <Technologies>
                        <Left>
                            <ul>
                                <li>
                                    <TechnologiName>HTML CSS</TechnologiName>
                                    <TechnologiState>
                                        <ActiveImg
                                            src={Active}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>JavaScript</TechnologiName>
                                    <TechnologiState>
                                        <ActiveImg
                                            src={Active}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>React.js</TechnologiName>
                                    <TechnologiState>
                                        <DontActiveImg
                                            src= {DontActive}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>jQuery</TechnologiName>
                                    <TechnologiState>
                                        <ActiveImg
                                            src={Active}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>Bootstrap 4</TechnologiName>
                                    <TechnologiState>
                                        <DontActiveImg
                                            src= {DontActive}
                                        />
                                    </TechnologiState>
                                </li>
                            </ul>
                        </Left>
                        <Line/>
                        <Right>
                            <ul>
                                <li>
                                    <TechnologiName>PHP</TechnologiName>
                                    <TechnologiState>
                                        <DontActiveImg
                                            src= {DontActive}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>Python</TechnologiName>
                                    <TechnologiState>
                                        <DontActiveImg
                                            src= {DontActive}
                                        />
                                    </TechnologiState>
                                </li>
                                <li>
                                    <TechnologiName>Node.js</TechnologiName>
                                    <TechnologiState>
                                        <DontActiveImg
                                            src= {DontActive}
                                        />
                                    </TechnologiState>
                                </li>
                            </ul>

                        </Right>
                    </Technologies>
                </div>
            </Info>
        )
    }
}
const ActiveImg = styled.img`

`
const DontActiveImg = styled.img`

`
const Info = styled.div`
    width: 1400px;
    margin: 0 auto;
    z-index: 2;
    position: relative;
    top: 13vh;
    display: flex;
    justify-content: space-between;
`
const Description = styled.div`
    width: 935px;
`
const DescriptionTitle = styled.div`
    font-size: 24px;
    margin-left: 50px;
    color: white;
    font-family: sfBold;
    letter-spacing: 1px;
`
const Text = styled.div`
    color: white;
    font-size: 18px;
    font-family: sfRegular;
    margin-top: 30px;
    letter-spacing: 1.3px;
    line-height: 22px;
`
const TechnologiesTitle = styled.div`
    color: white;
    font-size: 24px;
    font-family: sfBold;
    margin-left: 20px;
    letter-spacing: 1px;
`
const Technologies = styled.div`
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    height: 144px;
`
const Left = styled.div`
    ul{
        list-style: none;
        padding: 5px 0;
        margin: 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        li{
            display: flex;
            justify-content: space-between;
            width: 160px;
            padding-top: 8px;
            position: relative;
            cursor: context-menu;
            :first-child{
                padding-top: 0;
            }
            ::before{
                content: '';
                position: absolute;
                bottom: -2px;
                right: 80px;
                width: 0px;
                height: 1px;
                background-color: rgba(255,255,255,0.5);
                transition: width 0.2s ease;
            }
            ::after{
                content: '';
                position: absolute;
                bottom: -2px;
                left: 80px;
                width: 0px;
                height: 1px;
                background-color: rgba(255,255,255,0.5);
                transition: width 0.2s ease;
            }
            :hover{
                ::before{
                    width: 80px;
                }
                ::after{
                    width: 80px;
                }
            }

        }
    }
`
const TechnologiName = styled.span`
    color: white;
    font-size: 18px;
    font-family: sfRegular;
    letter-spacing: 1px;
`
const TechnologiState = styled.span`
    user-select: none;
`
const Line = styled.div`
    width: 2px;
    background-color: rgba(255,255,255,.5);
    margin: 0 15px;
`
const Right = styled.div`
    ul{
        list-style: none;
        padding: 5px 0;
        margin: 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        li{
            display: flex;
            justify-content: space-between;
            width: 160px;
            padding-top: 8px;
            position: relative;
            cursor: context-menu;
            :first-child{
                padding-top: 0;
            }
            ::before{
                content: '';
                position: absolute;
                bottom: -2px;
                right: 80px;
                width: 0px;
                height: 1px;
                background-color: rgba(255,255,255,0.5);
                transition: width 0.2s ease;
            }
            ::after{
                content: '';
                position: absolute;
                bottom: -2px;
                left: 80px;
                width: 0px;
                height: 1px;
                background-color: rgba(255,255,255,0.5);
                transition: width 0.2s ease;
            }
            :hover{
                ::before{
                    width: 80px;
                }
                ::after{
                    width: 80px;
                }
            }
        }
    }
`