import React, { Component } from 'react'
import styled from 'styled-components'
import Scrool from './Scrool'

export default class MenuPlusScrool extends Component {
    render() { 
        return ( 
            <Rigth
                className='wow fadeIn'
            >
                <Menu>
                    <div>
                    </div>
                </Menu>
                {/* <Scrool/> */}
            </Rigth>
        );
    }
}
const Rigth = styled.div`
    width: 50px;
    height: 80vh;
    position: fixed;
    right: 75px;
    top: 50vh;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
`
const Menu = styled.div`
    width: 50px;
    height: 30px;
    transform: translate(-50%, -50%);
    position: absolute;
    left: 27px;
    top: 0;
    cursor: pointer;
    :hover>div{
        ::before{
            content:'';
            background-color: white;
            width: 45px;
            right: 0;
            height: 2px;
            bottom: 8px;
            position: absolute;
        }
        ::after{
            content:'';
            background-color: white;
            width: 40px;
            right: 0;
            height: 2px;
            top: 8px;
            position: absolute;
        }
    }
    div{
        background-color: white;
        width: 30px;
        height: 2px;
        left: 35%;
        top: 50%;
        position: absolute;
        ::before{
            content:'';
            background-color: white;
            width: 45px;
            right: 0;
            height: 2px;
            bottom: 10px;
            position: absolute;
        }
        ::after{
            content:'';
            background-color: white;
            width: 40px;
            right: 0;
            height: 2px;
            top: 10px;
            position: absolute;
        }
    }
`