import React, {Component} from 'react'
import styled from 'styled-components'
import Person from './img/p.jpg'


export default class Content extends Component{

    
    render(){

        return(
            <SliderContent>
                <PersonImg
                        className="wow fadeInUp">
                    <UserName>MOTNIN VASYL</UserName>
                    <img
                        src={Person}
                        alt='Person'
                    />
                </PersonImg>
                <PersonSkill>
                    <TitleBlock
                        className="wow fadeInUp"
                    >
                        <Title>
                            SKILLS
                        </Title>
                        <Box/>
                    </TitleBlock>
                    <Skills>
                        <LeftList>
                            <ul>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>Photoshop:</SkillName>
                                        <SkillLevel>85%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "85%" }}/>
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>HTML CSS:</SkillName>
                                        <SkillLevel>99%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "99%" }}/>
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>JavaScript:</SkillName>
                                        <SkillLevel>97%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "97%" }}/>
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>React.js:</SkillName>
                                        <SkillLevel>75%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "75%" }}/>
                                    </SkillLevelLoad>
                                </li>
                            </ul>
                        </LeftList>
                        <Line
                            className="wow zoomIn"
                        />
                        <RightList>
                            <ul>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>OpenCart:</SkillName>
                                        <SkillLevel>83%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "83%" }}
                                        />
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>Bootstrap 4:</SkillName>
                                        <SkillLevel>99%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "99%" }} />
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>jQuery:</SkillName>
                                        <SkillLevel>87%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "87%" }} />
                                    </SkillLevelLoad>
                                </li>
                                <li
                                    className="wow zoomIn"
                                >
                                    <SkillNameBlock>
                                        <SkillName>English language:</SkillName>
                                        <SkillLevel>40%</SkillLevel>
                                    </SkillNameBlock>

                                    <SkillLevelLoad>
                                        <LevelLoad 
                                            className="wow slideInLeft"
                                            data-wow-delay="0.4s"
                                            data-wow-duration="2s"
                                            style={{ width: "40%" }} />
                                    </SkillLevelLoad>
                                </li>
                            </ul>

                        </RightList>
                    </Skills>
                </PersonSkill>
            </SliderContent>
        )
    }
}

const SliderContent = styled.div`
    display: flex;
    align-items: flex-end;
`

const PersonImg = styled.div`
`

const UserName = styled.span`
    margin-bottom: 10px;
    display: block;
    color: white;
    font-family: sfMedium;
    font-size: 16px;
`

const PersonSkill = styled.div`
    margin-left: 45px;
`
// TitleBlock -----------------------------
const TitleBlock = styled.div`

`
const Title = styled.span`
    color: white;
    font-size: 24px;
    font-family: sfBold;
    z-index: 1;
    position: relative;
    margin-left: 10px;
    letter-spacing: 2px;
`
const Box = styled.div`
    border: 2px solid rgba(103, 98, 28,0.6);
    width: 65px;
    height: 23px;
    position: relative;
    top: -19px;
    z-index: 0;
`
// Skills ---------------------------------
const Skills = styled.div`
    display: flex;
    justify-content: space-between;
`
const LeftList = styled.div`
    ul{
        margin: 0;
        padding-left: 0;
        list-style: none;
        li{
            width: 300px;
            margin-top: 20px;
            transition: transform 0.1s ease;
            :first-child{
                margin-top: 0;
            }
            :hover{
                transform: scale(1.04)
            }
        }
    }
`
const SkillNameBlock = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
`
const SkillName = styled.span`
    color: white;
    font-family: sfMedium;
    font-size: 16px;
    letter-spacing: 1px;
`
const SkillLevel = styled.span`
    color: white;
    font-family: sfMedium;
    font-size: 16px;
    letter-spacing: 1px;
`
const SkillLevelLoad = styled.div`
    margin-top: 10px;
    height: 7px;
    border-radius: 5px;
    background-color: #535353;
    overflow: hidden;
`
const LevelLoad = styled.div`
    background-color: white;
    height: 100%;
    border-radius: 5px;
`

const Line = styled.div`
    width: 1px;
    height: 210px;
    background-color: white;
    margin: auto 25px;
`
const RightList = styled.div`
    ul{
        margin: 0;
        padding-left: 0;
        list-style: none;
        li{
            width: 300px;
            margin-top: 20px;
            transition: transform 0.1s ease;
            :first-child{
                margin-top: 0;
            }
            :hover{
                transform: scale(1.04)
            }
        }
    }
`


