import React, {Component} from 'react'
import styled from 'styled-components'

export default class Launge extends Component {

    arrLaungeName = ['eng','ua','rus']

    render(){
        const {ActiveLanguage, newLaunge} = this.props
        
        let Li = this.arrLaungeName.map(e => {
            return(
                <li
                    key={e}
                    className={e===ActiveLanguage?'Active':''}
                    onClick={()=>newLaunge(e)}
                >{e}</li>
            )
        });
        
        return(
            <LaungeBlock
                className="wow fadeInDown"
            >
                <ul>
                    {Li}
                </ul>
            </LaungeBlock>
        )
    }
}
let LaungeBlock = styled.div`

    height: 20px;
    width: 200px;
    margin-left: 250px;
    margin-top: 8.5vh;
    ul{
        list-style: none;
        color: #676767;
        font-family: sfLight;
        display: flex;
        padding: 0;
        margin: 0;
        position: relative;
        .Active{
            color: white;
        }
        li{
            margin: 0 6px;
            font-size: 16px;
            cursor:pointer;
            user-select:none;
            text-transform: uppercase;
            :hover{
                color: #C1C1C1;
            }
            :first-child{
                margin-left:0px;
            }
            :last-child{
                margin-right: 0px;
                ::after{
                    display:none;
                }
            }
            ::after{
                content:'';
                position: absolute;
                top: 3px;
                margin-left: 6px;
                height:13px;
                width: 1px;
                background-color: #676767;
            }
        }
    }
`