import React,{Component} from 'react'
import styled from 'styled-components'
import Buttons from './Buttons'

export default class Content extends Component{
    render(){
        const {Language} = this.props
        
        return(
            <ContentBlock
            >
                <Hello
                    className='wow fadeInLeft'
                >{Language.paragraph}</Hello>
                <Logo
                    className='wow fadeInLeft'
                >{Language.logoLeft} <br/> {Language.logoRight}</Logo>
                <Commit
                    className='wow fadeInLeft'
                >{Language.commit}</Commit>
                <Buttons
                    Language = {Language["Buttons"]}
                />
            </ContentBlock>
        )
    }
}
const ContentBlock = styled.div`
    margin-top: 25vh;
    margin-left: 250px;
    z-index: 5;
`
const Hello = styled.p`
    margin: 0;
    font-size: 18px;
    font-family: sfLight;
    color: white;
    margin-left: 5px;
`
const Logo = styled.h1`
    margin: 0;
    padding: 0;
    margin-top: 40px;
    font-size: 60px;
    color: white;
    font-family: sfHeavy;
`
const Commit = styled.p`
    margin: 0;
    padding: 0;
    margin-top: 18px;
    font-size: 16px;
    color: #929292;
    font-family: sfLight;
    letter-spacing: 4px;
`
